import React, { useState, useEffect } from "react";
import { Helmet } from "react-helmet-async";
import { useParams } from "react-router-dom";
import HtmlRender from "../components/HtmlRender";
import axios from "axios";
import CONSTANTS from "../assets/constants.json";
import MarkupEditor from "../components/MarkupEditor";
import { useNavigate } from "react-router-dom";
import { AuthticationService } from "../services/auth";

function Newpost({ posts }) {
  const [postData, setPostData] = useState({
    title: "",
    content: "",
    author: "",
    cover: "",
    image1: "",
    image2: "",
    image3: "",
    image4: "",
  });

  const [isUpdate, setIsUpdate] = useState(false);

  const { id } = useParams();

  useEffect(() => {
    if (id) {
      const post = posts?.find((post) => post.id === parseInt(id));
      if (post) {
        setPostData({ ...post });
      }
      setIsUpdate(true);
    } else {
      setIsUpdate(false);
    }
  }, [id, posts]);

  const [message, setMessage] = useState("");

  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();
  if (!AuthticationService.isEditor) {
    // If the user is not authenticated, redirect to the login page
    AuthticationService.logout(() => {
      navigate("/login");
    });
  }

  const handleChange = (e) => {
    const { name, value } = e.target;
    setPostData({
      ...postData,
      [name]: value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    if (
      postData.title.trim() === "" ||
      postData.content.trim() === "" ||
      postData.author.trim() === ""
    ) {
      alert("Please fill out all fields.");
      setLoading(false);
      return;
    }

    try {
      if (isUpdate) {
        const response = await axios.post(
          CONSTANTS.API_URL,
          JSON.stringify({
            method: "update",
            ...postData,
          })
        );
        setMessage(response.data);
      } else {
        const response = await axios.post(
          CONSTANTS.API_URL,
          JSON.stringify({
            method: "create",
            ...postData,
          })
        );
        setMessage(response.data);
      }

      // Set a success message or display response from the server
    } catch (error) {
      console.error(
        `There was an error ${isUpdate ? "updating" : "creating"} the post!`,
        error
      );
      setMessage(
        `Failed to ${isUpdate ? "update" : "create"} post. Please try again.`
      );
    } finally {
      // Set loading back to false after request is complete
      setLoading(false);
    }
  };

  return (
    <div className="mx-auto p-1 md:p-0 md:m-0">
      <Helmet>
        <title> {isUpdate ? "Update Post" : "Add New Post"} </title>
        <meta
          name="description"
          content={isUpdate ? "Update Post" : "Add New Post"}
        />
      </Helmet>
      <div className="grid grid-cols-1 p-2 md:px-24 md:py-4 pt-4">
        <h1 className="text-4xl font-bold mb-6">
          {isUpdate ? "Update Post" : "Add New Post"}
        </h1>
        <form
          onSubmit={handleSubmit}
          className="p-6 shadow-md rounded-lg bg-white dark:bg-slate-600 text-black dark:text-white border-2 border-blue-600/[0.2] dark:border-slate-900 shadow-md"
        >
          <div className="grid grid-cols-2 gap-x-2">
            <div className="mb-4">
              <label
                className="block text-gray-700 dark:text-gray-100 text-sm font-bold mb-2"
                htmlFor="title"
              >
                Title
              </label>
              <input
                name="title"
                type="text"
                value={postData.title}
                onChange={handleChange}
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 dark:text-gray-100 dark:bg-slate-800 leading-tight focus:outline-none focus:shadow-outline"
                placeholder="Enter post title"
                required
              />
            </div>
            <div className="mb-4">
              <label
                className="block text-gray-700 dark:text-gray-100 text-sm font-bold mb-2"
                htmlFor="title"
              >
                Author
              </label>
              <input
                name="author"
                type="text"
                value={postData.author}
                onChange={handleChange}
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 dark:text-gray-100 dark:bg-slate-800 leading-tight focus:outline-none focus:shadow-outline"
                placeholder="Enter post title"
                required
              />
            </div>
            <div className="mb-4">
              <label
                className="block text-gray-700 dark:text-gray-100 text-sm font-bold mb-2"
                htmlFor="cover"
              >
                Cover
              </label>
              <input
                name="cover"
                type="text"
                value={postData.cover}
                onChange={handleChange}
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 dark:text-gray-100 dark:bg-slate-800 leading-tight focus:outline-none focus:shadow-outline"
                placeholder="Enter Cover"
              />
            </div>
            <div className="mb-4">
              <label
                className="block text-gray-700 dark:text-gray-100 text-sm font-bold mb-2"
                htmlFor="image1"
              >
                Image 1
              </label>
              <input
                name="image1"
                type="text"
                value={postData.image1}
                onChange={handleChange}
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700  dark:text-gray-100 dark:bg-slate-800 leading-tight focus:outline-none focus:shadow-outline"
                placeholder="Enter Image 1"
              />
            </div>
            <div className="mb-4">
              <label
                className="block text-gray-700 dark:text-gray-100 text-sm font-bold mb-2"
                htmlFor="image2"
              >
                Image 2
              </label>
              <input
                name="image2"
                type="text"
                value={postData.image2}
                onChange={handleChange}
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 dark:text-gray-100 dark:bg-slate-800 leading-tight focus:outline-none focus:shadow-outline"
                placeholder="Enter post image2"
              />
            </div>
            <div className="mb-4">
              <label
                className="block text-gray-700 dark:text-gray-100 text-sm font-bold mb-2"
                htmlFor="image3"
              >
                Image 3
              </label>
              <input
                name="image3"
                type="text"
                value={postData.image3}
                onChange={handleChange}
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 dark:text-gray-100 dark:bg-slate-800 leading-tight focus:outline-none focus:shadow-outline"
                placeholder="Enter post image3"
              />
            </div>
            <div className="mb-4">
              <label
                className="block text-gray-700 dark:text-gray-100 text-sm font-bold mb-2"
                htmlFor="image4"
              >
                Image 4
              </label>
              <input
                name="image4"
                type="text"
                value={postData.image4}
                onChange={handleChange}
                className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 dark:text-gray-100 dark:bg-slate-800 leading-tight focus:outline-none focus:shadow-outline"
                placeholder="Enter post image4"
              />
            </div>
          </div>
          <div className="container">
            <MarkupEditor
              markup={postData.content}
              handleChange={handleChange}
            />
          </div>
          <div className="flex items-center justify-between">
            <button
              type="submit"
              className="bg-blue-500 hover:bg-blue-700 dark:hover:bg-blue-900 dark:bg-slate-800 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
              disabled={loading}
            >
              {loading
                ? "Submitting..."
                : isUpdate
                ? "Update Post"
                : "Add Post"}
            </button>
            {message && <p>{message}</p>}
          </div>
        </form>

        <div className="container web-view bg-white p-6 bg-white border-2 dark:border-slate-900 border-blue-600/[0.2] dark:bg-slate-600 shadow-md rounded-lg mt-6">
          <HtmlRender html={postData.content} />
        </div>
      </div>
    </div>
  );
}

export default Newpost;
