import React, { useState } from "react";

import { Link, useNavigate } from "react-router-dom";
import { AuthticationService } from "../services/auth";

function Navbar({ changeTheme, isDarkTheme }) {
  const themeChange = () => {
    changeTheme();
  };

  const navigate = useNavigate();
  const liteThemeTailwindClasses = "before:content-['☀️'] before:justify-start";
  const darkThemeTailwindClasses = "before:content-['🌙'] before:justify-end";

  const handleLogout = () => {
    setPagesOpen(false);
    AuthticationService.logout(() => {
      navigate("/login");
    });
  };
  const [isPagesOpen, setPagesOpen] = useState(false);
  const [isDestinationsOpen, setDestinationsOpen] = useState(false);

  const doNothing = () => {};
  const showNotEditorMessage = () => {
    alert("You are not authorized to perform the action, Contact Admin!");
  };
  return (
    <header className="bg-blue-600/[0.8] border-blue-600/[0.5] dark:bg-slate-800 dark:border-slate-400/[0.5] dark:text-black md:p-4 w-full sticky top-0 z-50 border-b-2  shadow-xl">
      <nav
        className={`flex container justify-start p-1 align-center items-center h-12
           ${
             AuthticationService.isAuthenticated
               ? " md:justify-center"
               : " md:justify-end"
           }
          `}
      >
        {/* Logo */}

        {/* Mobile Menu Items */}
        <div className="md:hidden space-x-8 md:space-x-24  w-full">
          <div className="relative flex justify-between">
            {!AuthticationService.isAuthenticated && (
              <Link
                to="/"
                className="px-4 py-4 text-black dark:text-slate-400 font-bold"
                onClick={() => setPagesOpen(false)}
              >
                Home
              </Link>
            )}
            {AuthticationService.isAuthenticated && (
              <button
                onClick={() => setPagesOpen(!isPagesOpen)}
                onMouseEnter={() => setPagesOpen(!isPagesOpen)}
                onMouseLeave={() => setPagesOpen(!isPagesOpen)}
                className="text-white hover:text-gray-300 focus:outline-none text-5xl px-2"
              >
                ≡
              </button>
            )}
            {isPagesOpen && (
              <div
                className="absolute left-0 flex flex-col flex-wrap w-48 bg-blue-600/[0.8] dark:bg-slate-800 dark:border-slate-400/[0.5] dark:text-black rounded-lg shadow-lg font-bold text-2xl"
                onMouseEnter={() => setPagesOpen(true)}
                onMouseLeave={() => setPagesOpen(false)}
              >
                <Link
                  to=""
                  className="px-4 py-4 text-white hover:bg-gray-200"
                  onClick={() => setPagesOpen(false)}
                >
                  Home
                </Link>
                <Link
                  to="/about"
                  className="px-4 py-4 text-white hover:bg-gray-200"
                  onClick={() => setPagesOpen(false)}
                >
                  About
                </Link>
                <Link
                  to={AuthticationService.isEditor ? "/new-post" : "#"}
                  className="px-4 py-4 text-white hover:bg-gray-200"
                  onClick={() => {
                    setPagesOpen(false);
                    showNotEditorMessage();
                  }}
                >
                  Add New
                </Link>

                <div className="flex space-x-6">
                  <p
                    onClick={handleLogout}
                    className="px-4 py-4 text-white hover:bg-gray-200"
                  >
                    Logout
                  </p>
                </div>
              </div>
            )}
            <div className="ml-auto flex items-center float-right">
              <input
                type="checkbox"
                className="hidden"
                onChange={themeChange}
              />
              <label
                htmlFor="darkModeSwitch"
                className="text-white cursor-pointer flex items-center"
              >
                <span
                  id="darkModeHandler"
                  className={`relative ml-2 ease-in duration-3000 before:w-12 before:absolute before:flex before:h-5 before:bg-white before:right-0 before:top-px before:rounded-xl before:items-center before:text-red-500 before:ease-in before:duration-3000 ${
                    isDarkTheme
                      ? darkThemeTailwindClasses
                      : liteThemeTailwindClasses
                  }`}
                >
                  {isDarkTheme ? "Dark" : "Lite"}
                </span>
              </label>
            </div>
          </div>
        </div>
        {/* Desktop Menu Items */}
        <div className="hidden md:flex md:flex-wrap md:items-center place-items-center space-x-8 md:gap-12 font-serif">
          {AuthticationService.isAuthenticated && (
            <React.Fragment>
              <Link to="" className="text-white font-bold hover:text-gray-300">
                Home
              </Link>
              <Link
                to="/about"
                className="text-white font-bold hover:text-gray-300"
              >
                About
              </Link>
              <Link
                to="/"
                className="text-white font-bold text-4xl font-bold font-mono"
              >
                Journey
              </Link>
              <Link
                to={AuthticationService.isEditor ? "/new-post" : "#"}
                className="text-white font-bold"
                onClick={() => {
                  !AuthticationService.isEditor
                    ? showNotEditorMessage()
                    : doNothing();
                }}
              >
                Add New
              </Link>
              <div className="flex space-x-6">
                <p
                  onClick={handleLogout}
                  className="text-white font-bold hover:text-gray-300 cursor-pointer"
                >
                  Logout
                </p>
              </div>
            </React.Fragment>
          )}
          <div className="ml-auto flex items-center">
            <input
              type="checkbox"
              id="darkModeSwitch"
              className="hidden"
              onChange={themeChange}
            />
            <label
              htmlFor="darkModeSwitch"
              className="text-white cursor-pointer flex items-center"
            >
              <span
                id="darkModeHandler"
                className={`relative text-sm md:text-xs ml-2 ease-in duration-3000 before:w-12 before:absolute before:flex before:h-5 before:bg-white before:right-0 before:top-px before:rounded-xl before:items-center before:text-red-500 before:ease-in before:duration-3000 ${
                  isDarkTheme
                    ? darkThemeTailwindClasses
                    : liteThemeTailwindClasses
                }`}
              >
                {isDarkTheme ? "Dark" : "Lite"}
              </span>
            </label>
          </div>
        </div>
      </nav>
    </header>
  );
}

export default Navbar;
