import React, { Suspense, useEffect, useState } from "react";
import { Routes, Route, HashRouter } from "react-router-dom";
import Navbar from "./components/Navbar";
import Home from "./pages/Home";
import Footer from "./components/Footer";
import "@fortawesome/fontawesome-free/css/all.min.css";
import Newpost from "./pages/Newpost";
import Login from "./pages/Login";
import Dashboard from "./pages/Dashboard";
import PrivateRoute from "./components/PrivateRoute";
import CONSTANTS from "./assets/constants.json";
import PrivacyPolicy from "./pages/PrivacyPolicy";
import NotFound from "./pages/NotFound";
import "./App.css";
import ScrollToTop from "./components/ScrollToTop";

const About = React.lazy(() => import("./pages/About"));
const Destination = React.lazy(() => import("./pages/Destination"));
const Post = React.lazy(() => import("./pages/Post"));

function App() {
  const [blogData, setBlogData] = useState([]);
  const [postCount, setPostCount] = useState(CONSTANTS.INITIAL_POST_COUNT);

  const [isDarkTheme, setIsDarkTheme] = useState(false);
  const [shownPosts, setShownPosts] = useState(
    blogData.slice(0, CONSTANTS.INITIAL_POST_COUNT)
  );
  useEffect(() => {
    if ((blogData, postCount)) {
      setShownPosts(blogData.slice(0, postCount));
    }
  }, [postCount, blogData]);

  function getMorePosts() {
    setPostCount(postCount + 1);
  }

  useEffect(() => {
    async function fetchData() {
      try {
        const apiData = await fetch(CONSTANTS.API_URL);
        const json = await apiData.json();

        setBlogData(json.sort((a, b) => new Date(b.date) - new Date(a.date)));
      } catch (error) {
        alert("The App may not be usable");
      }
    }

    function detectTheme() {
      const darkThemeMq = window.matchMedia("(prefers-color-scheme: dark)");
      if (darkThemeMq.matches) {
        setIsDarkTheme(true);
      } else {
        setIsDarkTheme(false);
      }
    }
    fetchData();
    detectTheme();
  }, []);

  const handleScroll = (e) => {
    const postsContainer = document.getElementById("postsContainer");
    if (postsContainer) {
      const rect = postsContainer.getBoundingClientRect();
      if (
        rect.bottom <= window.innerHeight - 100 &&
        postCount < blogData.length
      ) {
        setTimeout(() => {
          getMorePosts();
        }, 500);
      }
    }
  };

  const changeTheme = () => {
    setIsDarkTheme((last) => !last);
  };

  return (
    <HashRouter>
      <ScrollToTop>
        <div
          id="scrollHandler"
          onScroll={handleScroll}
          className={`max-h-screen overflow-auto bg-white dark:bg-slate-600 ${
            isDarkTheme ? "dark" : ""
          }`}
        >
          <Navbar changeTheme={changeTheme} isDarkTheme={isDarkTheme} />
          <main className="bg-blue-200/[0.1] dark:bg-slate-600">
            <Suspense fallback={<div>Loading...</div>}>
              <div className="flex-grow">
                <Routes>
                  <Route
                    path="/"
                    element={
                      <PrivateRoute>
                        <Home posts={shownPosts} />
                      </PrivateRoute>
                    }
                  />
                  <Route path="/about" element={<About />} />
                  <Route
                    path="/destination"
                    element={
                      <PrivateRoute>
                        <Destination />
                      </PrivateRoute>
                    }
                  />
                  <Route
                    path="/post/:id"
                    element={
                      <PrivateRoute>
                        <Post posts={blogData} />
                      </PrivateRoute>
                    }
                  />
                  <Route
                    path="/new-post"
                    element={
                      <PrivateRoute>
                        <Newpost />
                      </PrivateRoute>
                    }
                  />
                  <Route
                    path="/edit-post/:id" // Dynamic postId parameter
                    element={
                      <PrivateRoute>
                        <Newpost posts={blogData} />
                      </PrivateRoute>
                    }
                  />
                  <Route path="/login" element={<Login />} />
                  <Route path="/privacy-policy" element={<PrivacyPolicy />} />
                  <Route
                    path="/dashboard"
                    element={
                      <PrivateRoute>
                        <Dashboard />
                      </PrivateRoute>
                    }
                  />
                  <Route path="*" element={<NotFound />}></Route>
                </Routes>
              </div>
            </Suspense>
          </main>
          <Footer />
        </div>
      </ScrollToTop>
    </HashRouter>
  );
}

export default App;
