// src/pages/Login.js
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { AuthticationService } from "../services/auth";

function Login() {
  const navigate = useNavigate();

  const [loggingIn, setLoggingIn] = useState(false);
  const [creds, setCreds] = useState({
    username: "",
    password: "",
  });
  const handleChange = (e) => {
    const { name, value } = e.target;
    setCreds({
      ...creds,
      [name]: value,
    });
  };

  const handleLogin = () => {
    if (creds.username.trim() === "" || creds.password.trim() === "") {
      alert("Please fill out all fields.");
      setLoggingIn(false);
      return;
    }
    setLoggingIn(true);
    AuthticationService.login(creds, (isAuthenticated, loginMessage) => {
      alert(loginMessage);
      setLoggingIn(false);
      if (isAuthenticated) {
        navigate("/");
      }
    });
  };

  return (
    <div className="container flex justify-center mx-auto p-12 bg-white dark:bg-slate-500">
      <form
        className="form p-6 border-2 shadow-md rounded-lg flex flex-col gap-2 bg-white dark:bg-slate-800 dark:text-white border-blue-600/[0.2]"
        onSubmit={handleLogin}
      >
        <h1 className="text-4xl font-bold mb-4 text-center dark:text-slate-400">Login</h1>
        <input
          className="border-2 rounded p-1 text-black dark:bg-slate-400"
          name="username"
          type="text"
          value={creds.username}
          onChange={handleChange}
          required
        />
        <input
          className="border-2 rounded p-1 text-black dark:bg-slate-400"
          name="password"
          type="password"
          value={creds.password}
          onChange={handleChange}
          required
        />
        <button
          className="bg-blue-500 text-white dark:bg-slate-400 dark:text-black px-4 py-2 mt-4 rounded"
          onClick={handleLogin}
          disabled={loggingIn}
        >
          {loggingIn ? "Logging In.." : "Login"}
        </button>
      </form>
    </div>
  );
}

export default Login;
