import React from "react";
import { Helmet } from "react-helmet-async";
import Hero from "../components/Hero";
import Thumbnail from "../components/Thumbnail";
import ShimmerThumb from "../components/ShimmerThumbnail";

function Home({ posts }) {
  return (
    <div className="m-0 p-0 w-full">
      <Helmet>
        <title>My Journey!</title>
        <meta
          name="description"
          content="Read the latest blog posts about Discovery, Adventure and Realisation."
        />
      </Helmet>
      <Hero image="https://picsum.photos/800/400" />
      <div
        className="grid grid-cols-1 p-2 md:px-24 md:py-12 bg-white dark:bg-slate-600 border-blue-600/[0.2]"
        id="postsContainer"
      >
        {posts.length === 0 ? (
          <React.Fragment>
            {[1, 2, 3].map((i) => (
              <ShimmerThumb index={i} key={i} />
            ))}
          </React.Fragment>
        ) : (
          posts &&
          posts.map((post) => (
            <div key={post.id} className="p-4 rounded-lg">
              <Thumbnail post={post} />
            </div>
          ))
        )}
      </div>
    </div>
  );
}

export default Home;
