// src/services/auth.js

import axios from "axios";

import CONSTANTS from "../assets/constants.json";
import localStorageService from "./localStorageService";
export const AuthticationService = {
  isAuthenticated: false,
  isEditor: false,

  async login(credentials, callback) {
    const response = await axios.post(
      CONSTANTS.API_URL,
      JSON.stringify({
        method: "login",
        ...credentials,
      })
    );
    const { token, message } = response.data;
    if (token) {
      localStorageService.setToken(token);
      this.isValidUser();
    }
    setTimeout(callback(this.isAuthenticated, message), 100); // Simulate async login
  },

  logout(callback) {
    this.isAuthenticated = false;
    localStorageService.removeToken();
    setTimeout(callback, 100); // Simulate async logout
  },

  isValidUser() {
    const token = localStorageService.getToken();
    if (token) {
      const [username, dateCreatedString] = atob(token).split(":");
      const dateTokens = dateCreatedString.split(" ");
      const formedString = `${dateTokens[1]} ${dateTokens[2]} ${dateTokens[3]}`;
      const [dateCreated, today] = [
        new Date(formedString),
        new Date(),
      ];
      this.isAuthenticated =
        dateCreated.getDate() === today.getDate() &&
        dateCreated.getMonth() === today.getMonth() &&
        dateCreated.getFullYear() === today.getFullYear() &&
        ["ashish","public",'susmita','aishani'].includes(username);

        this.isEditor = ['ashish'].includes(username)
    }
  },
};
