import React, { useState } from "react";

function HtmlRender({ html, excerptLength, isExcerpt }) {
  const [clickedImageSrc, setClickedImageSrc] = useState(null);
  let content = isExcerpt ? html.substring(0, excerptLength) + " ..." : html;
  const clickHandler = (e) => {
    e.target.closest("img") && setClickedImageSrc((prev) => e.target.src);
  };

  return (
    <React.Fragment>
      <div
        onClick={clickHandler}
        dangerouslySetInnerHTML={{
          __html: content,
        }}
      ></div>
      {clickedImageSrc && (
        <div
          className="relative z-10"
          aria-labelledby="modal-title"
          role="dialog"
          aria-modal="true"
        >
          <div
            className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"
            aria-hidden="true"
          ></div>

          <div className="flex justify-center fixed inset-0 z-10 w-screen overflow-y-auto">
            <div className="flex items-center justify-center p-4 text-center sm:items-center sm:p-0">
              <div className="relative transform overflow-hidden rounded-lg text-left transition-all sm:my-8 sm:w-full sm:max-w-4xl">
                <img
                  src={clickedImageSrc}
                  alt="cover"
                  className="shadow-md rounded-xl w-full h-full max-h-[90vh] object-contain"
                />
              </div>
              <button
                type="button"
                className="absolute bottom-0 mt-3 inline-flex w-full justify-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50 sm:mt-0 sm:w-auto"
                onClick={() => setClickedImageSrc((prev) => null)}
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </React.Fragment>
  );
}

export default HtmlRender;
