import React, { useState } from "react";

function MarkupEditor({ markup, handleChange }) {
  const handleTextAreaChange = (e) => {
    handleChange(e);
  };
  const [elementData, setElementData] = useState({
    element: "",
    classes: "",
    text: "",
  });

  const createCompleteHTMLElement = ({ element, classes, text }) => {
    let res = "";
    let classToken = "";
    if (element) {
      switch (element) {
        case "p":
          if (classes) {
            classToken = ` class='${classes}'`;
          }
          res += `<p${classToken}>${text}</p>`;
          break;
        case "h1":
          classes = "text-4xl " + classes;
          classToken += ` class='${classes}'`;
          res += `<h1${classToken}>${text}</h1>`;
          break;
        case "h2":
          classes = "text-3xl " + classes;
          classToken = ` class='${classes}'`;
          res += `<h2${classToken}>${text}</h2>`;
          break;
        case "h3":
          classes = "text-2xl " + classes;
          classToken = ` class='${classes}'`;
          res += `<h3${classToken}>${text}</h3>`;
          break;
        case "h4":
          classes = "text-xl " + classes;
          classToken = ` class='${classes}'`;
          res += `<h4${classToken}>${text}</h4>`;
          break;
        case "div":
          if (classes) {
            classToken = ` class='${classes}'`;
          }
          res += `<div${classToken}>${text}</div>`;
          break;
        case "span":
          if (classes) {
            classToken = ` class='${classes}'`;
          }
          res += `<span${classToken}>${text}</span>`;
        default:
          res += ``;
          break;
      }
    }
    return res;
  };

  const insertAtCursor = (myField, myValue) => {
    if (document.selection) {
      myField.focus();
      let sel = document.selection.createRange();
      sel.text = myValue;
    } else if (myField.selectionStart || myField.selectionStart == "0") {
      let startPos = myField.selectionStart;
      var endPos = myField.selectionEnd;
      myField.value =
        myField.value.substring(0, startPos) +
        myValue +
        myField.value.substring(endPos, myField.value.length);
    } else {
      myField.value += myValue;
    }
    handleTextAreaChange({ target: { name: "content", value: myField.value } });
  };

  const handleElementAdd = (e) => {
    e.preventDefault();
    if (elementData.element) {
      const el = createCompleteHTMLElement(elementData);
      const textareaHandler = document.getElementById("content");
      insertAtCursor(textareaHandler, el);
    }
  };

  const updateForm = (e) => {
    const { name, value } = e.target;
    setElementData({
      ...elementData,
      [name]: value,
    });
  };

  return (
    <div className="h-full w-full">
      <div
        id="controls"
        className="p-2 border-2 dark:border-slate-900 flex flex-col md:flex md:flex-row justify-between gap-2 align-center"
      >
        <select
          name="element"
          id="element-select"
          className="p-2 rounded bg-white dark:bg-slate-600 border-2 dark:border-slate-900"
          value={elementData.element}
          onChange={updateForm}
        >
          <option value="">--Please choose an element--</option>
          <option value="h1" className="p-1 rounded text-4xl">
            H1
          </option>
          <option value="h2" className="p-1 rounded text-3xl">
            H2
          </option>
          <option value="h3" className="p-1 rounded text-2xl">
            H3
          </option>
          <option value="h4" className="p-1 rounded text-xl">
            h4
          </option>
          <option value="div" className="p-1 rounded font-bold">
            DIV
          </option>
          <option value="p" className="p-1 rounded">
            P
          </option>

          <option value="span" className="p-1 rounded">
            span
          </option>
        </select>
        <input
          name="classes"
          placeholder="e.g 'p-1 border'"
          className="p-2 border-2 dark:border-slate-900 rounded md:flex-1 grow bg-white dark:bg-slate-600"
          value={elementData.classes}
          onChange={updateForm}
        />
        <input
          name="text"
          placeholder="e.g 'lorem ipsum'"
          className="p-2 border-2 dark:border-slate-900 rounded md:flex-1 bg-white dark:bg-slate-600"
          value={elementData.text}
          onChange={updateForm}
        />

        <button
          className="rounded-full border-2 dark:border-slate-900 bg-blue-500 hover:bg-blue-600 dark:hover:bg-blue-900 dark:bg-slate-800 px-12 shrink-1 py-1 self-center disabled:opacity-75"
          disabled={elementData.element === ""}
          onClick={handleElementAdd}
        >
          Add
        </button>
      </div>
      <textarea
        name="content"
        id="content"
        className="border-2 dark:border-slate-900 rounded html-editor w-full p-2 bg-white dark:bg-slate-600"
        rows="10"
        value={markup}
        onChange={handleTextAreaChange}
        required
      />
    </div>
  );
}

export default MarkupEditor;
